    border: 1px solid rgb(206, 204, 204);
}
.item-bar{
    border: none;
    width: 40%;
    padding: 5px;
}
.card{
   margin: auto;
   border: none !important;
}


.product-name{
   font-size: 14px;
}
.product-price{
font-size: 14px;
}
.product-name:hover{
   cursor: pointer;
   color: brown;
   text-decoration: underline;
}

.card:hover .details{
   border: 1px solid rgb(211, 211, 211);
}
.product-size{
   font-size: 14px;
   visibility: hidden;
}
.details:hover .product-size{
   visibility: visible;
}




* {
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
  }
.filter-container {
  border: 1px solid rgb(206, 204, 204);
}
.title {
  padding-left: 20px;
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
}
.filters {
  font-weight: 400;
  font-size: 16px;
  padding-left: 1.5rem;
  padding-right:  1rem;
  display: flex;
  justify-content: space-between;
}
.filters:hover {
 color: rgb(205, 4, 4);
}
.cat-icon{
    font-size: 25px;
}
.cat{
  display: none;
}
.filters:hover .cat{
    display: block;
}
.filter-name{
  font-size: 14px;
  font-family: Avenir-Roman;
  display: none;
  width: 100%;
  padding: 1.5rem 2rem 0rem 2rem;
}



.bg-grey{
    background: #e7eaee
}
ul{
    display: flex;
}
ul li{
    color: #000;
    font-size: 14px;
    font-weight: 500;
    text-transform: uppercase;
    list-style: none;
    display: inline;
    margin: 0 10px;
}
ul li:hover{
    color: rgb(188, 5, 5);
}
.footer{
    margin: auto;
}
.social-icons{
    font-size: 30px;
}
.social-icons:hover{
    color: rgb(188, 5, 5);
}
.icon-conatiner{
    display: flex;
    justify-content: space-between;
}
.footer-para{
    font-size: 12px;
}
a{
    text-decoration: none !important;
}
.copy{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 3rem;
}
.top{
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed; 
    width: 50px;
    height: 50px;
    left: 50%;
    bottom: 40px;
    font-size: 3rem;
    z-index: 1;
    cursor: pointer;
    border-radius: 50% !important;
}
.top-arrow{
    font-size: larger;
    font-weight: 700;
}
